import {  Route, useLocation, Switch } from "react-router-dom"
import { useTransition, animated} from "react-spring"
import Particles from "react-particles-js"


// components
import Home from "./components/jsx/Home"
import About from "./components/jsx/About"
import Contact from "./components/jsx/Contact"
import Projects from "./components/jsx/Projects"
import Header from "./components/jsx/Header"
import Resume from "./components/jsx/Resume"

import Protrac from "./components/jsx/projects/jsx/Protrac"
import Crypto from "./components/jsx/projects/jsx/Crypto"
import Pokedex from "./components/jsx/projects/jsx/Pokedex"
import Paint from "./components/jsx/projects/jsx/Paint"


import './App.css';



function App() {
  const location = useLocation()
  const transitions = useTransition(location, location => location.pathname, {
    from: {opacity:0, transform: "translateX(100%)"},
    enter: {opacity:1, transform: "translateX(0)"},
    leave: {opacity:0, transform: "translateX(-80%)"},
    config: {
      duration: 700
    }
  })
 
  return (
      <>
  
        <Header/>   
        {transitions.map(({item, props, key}) => (
          <animated.div key={key} style={props}>
             <Switch location={item}>
                <Route exact path="/" component={Home}/>
                <Route exact path="/about" component={About}/>
                <Route path="/work" component={Projects}/>
                <Route exact path="/contact" component={Contact}/>
                <Route exact path="/resume" component={Resume}/>
                {/* <Route exact path="/work/protrac" component={Protrac}/>
                <Route exact path="/work/cryptograph" component={Crypto}/>
                <Route exact path="/work/paintmagic" component={Paint}/>
                <Route exact path="/work/pokedex" component={Pokedex}/> */}
            </Switch>
          </animated.div>
        ))}
        
        <Particles
     params={{

       particles: {
         number: {
           value: 200,
           density: {
             enable: true,
             value_area: 2000
           }
         },
        collisions: {

        },
         line_linked: {
          enable: true,
          opacity: 1
         
        },
        size: {
          value: 2
        },
        move: {
          speed: 0.5,
          direction: "top",
          bounce: false,
          out_mode: "out",
          straight: false
        },
        opacity: {
          anim: {
            speed:2,
            opacity_min: 0
          }
        }
       },
       interactivity: {
         detect_on: "window",
        events: {
          onHover: {
            enable: true,
            mode: "repulse"
          },
          onClick: {
            enable: true,
            mode: ["push", "repulse"]
          }
        },
        modes: {
          grab: {
            distance: 500,
            lineLinked: {
              opacity: 0.5
            }
          },
          bubble: {
            distance: 200,
            size: 30,
            duration: 1,
            opacity: 1,
            speed: 12
          }
        }
       }

     }}
    id="particles-home"/>
  </>
  );
}

export default App;
