import React from 'react'
import "../css/Pokedex.css"
import ProjectsController from "../ProjectsController"
import img1 from "../../../../images/pokedex1.png"
import img2 from "../../../../images/pokedex2.png"
import img3 from "../../../../images/pokedex3.png"


const Pokedex = () => {
    return (
        <>
       <div className="title-btn">
    <a href="http://pokedex0813.herokuapp.com" rel="noreferrer" target="_blank"><button>view</button></a>
    <h1>Pokedex</h1>
    <a href="https://github.com/daniel-deru/project-pokedex" rel="noreferrer" target="_blank"><button>code</button></a>
    </div>
  
 
    <div className="project-wrapper">
        <div className="preview">
            <div className="img-container">
                <img src={img1} className="project-img" alt=""/>
                <img src={img2}  className="project-img" alt=""/>
                <img src={img3}  className="project-img" alt=""/>
            </div>
        </div>
        <aside className="project-content">
            {/* <h2>ProTrac</h2> */}
            <p>This website uses the pokedex API and uses several API calls that are connected. the focus is on simplicity and working with API's. I chose this particular API because it's a fun idea to play with and the API itself is very robust and complete even though that adds an extra layer of complexity to the logic. Tech Stack: HTML, CSS, JS, Axios</p>
        </aside>
    </div>
    </>
    )
}

export default Pokedex
