import React, {useState } from 'react'
import "../css/Header.css"
import { FaBars, FaTimes } from "react-icons/fa"
import { NavLink, Router } from "react-router-dom"

const Header = () => {
    const [isOpen, setIsOpen] = useState(false)
    const navSlider = () => setIsOpen(!isOpen)
    
    return (
        <header>
            <div>
                <FaBars className="menu-open-icon" onClick={navSlider}/>
            </div>
        <nav className={isOpen ? "open" : "closed"}>
            <FaTimes className="menu-close-icon" onClick={navSlider}/>
            <ul>
            
                <NavLink  exact to="/"  activeClassName="is-active" className="nav" onClick={navSlider}><li>Home</li></NavLink> 
                <NavLink to="/about"  activeClassName="is-active" className="nav" onClick={navSlider}><li>About</li></NavLink>
                <NavLink  to="/work/protrac"  activeClassName="is-active" className="nav" onClick={navSlider}><li>Work</li></NavLink>
                <NavLink to="/contact"  activeClassName="is-active" className="nav" onClick={navSlider}><li>Contact</li></NavLink>
                <NavLink to="/resume"  activeClassName="is-active" className="nav" onClick={navSlider}><li>Resume</li></NavLink>
            </ul>
        </nav>
     
    </header>

    )
}

export default Header
