import React from 'react'
import "../css/Crypto.css"
import ProjectsController from "../ProjectsController"
import img1 from "../../../../images/crypto1.png"
import img2 from "../../../../images/crypto2.png"
import img3 from "../../../../images/crypto3.png"


const Crypto = () => {
    return (
    <>
     <div className="title-btn">
    <a href="http://cryptograph-encoder-decoder.herokuapp.com" rel="noreferrer" target="_blank"><button>view</button></a>
    <h1>Cryptograph</h1>
    <a href="https://github.com/daniel-deru/project-cryptograph" rel="noreferrer" target="_blank"><button>code</button></a>
    </div>
  
 
    <div className="project-wrapper">
        <div className="preview">
            <div className="img-container">
                <img src={img1} className="project-img" alt=""/>
                <img src={img2}  className="project-img" alt=""/>
                <img src={img3}  className="project-img" alt=""/>
            </div>
        </div>
        <aside className="project-content">
            {/* <h2>ProTrac</h2> */}
            <p>This was a really fun project and deals with encoding and decoding secret messages. You can choose from several different bases like morse or binary. You can also enter a secret key (optional) but don't lose it or your message will never be decoded!! Check the info page on the website for more details. Tech Stack: Node, Express, JavaScript</p>
        </aside>
    </div>
    </>
    )
}

export default Crypto
