import React from 'react'
import "../css/Projects.css"
import {BrowserRouter, Route, Switch, NavLink, useRouteMatch, useHistory } from "react-router-dom"
import ProjectsController from "./projects/ProjectsController"

import Protrac from "./projects/jsx/Protrac"
import Crypto from "./projects/jsx/Crypto"
import Pokedex from "./projects/jsx/Pokedex"
import Paint from "./projects/jsx/Paint"


const Projects = () => {
  
    return (
    
        <div className="work-container">
            <section>
            {/* <h1>Projects</h1> */}
            <ProjectsController/>
               
                <div className="project-container">
                <Switch>
                    <Route  path="/work/protrac" component={Protrac}/>
                    <Route path="/work/cryptograph" component={Crypto}/>
                    <Route  path="/work/paintmagic" component={Paint}/>
                    <Route path="/work/pokedex" component={Pokedex}/>
                </Switch>
                </div>
            </section>
        </div>
     
    )
}

export default Projects
