import React from 'react'
import "../css/Paint.css"
import img1 from "../../../../images/paint1.png"
import img2 from "../../../../images/paint2.png"
import img3 from "../../../../images/paint3.png"


const Paint = () => {
    return (
        <>
        <div className="title-btn">
    <a href="http://paintmagic.herokuapp.com" rel="noreferrer" target="_blank"><button>view</button></a>
    <h1>Paint Magic</h1>
    <a href="https://github.com/daniel-deru/paintmagic" rel="noreferrer" target="_blank"><button>code</button></a>
    </div>
  
 
    <div className="project-wrapper">
        <div className="preview">
            <div className="img-container">
                <img src={img1} className="project-img" alt=""/>
                <img src={img2}  className="project-img" alt=""/>
                <img src={img3}  className="project-img" alt=""/>
            </div>
        </div>
        <aside className="project-content">
            <p>This website focuses on design and UI. It's a fictional website for an art school. this website was made with a mobile first approach and encorporates 3D interactive animations and other popular design trends. Tech Stack: Node, CSS, JS, React</p>
        </aside>
    </div>
        </>
    )
}

export default Paint
