import React from 'react'
import "../css/Protrac.css"
import ProjectsController from "../ProjectsController"
import img1 from "../../../../images/protrac1.png"
import img2 from "../../../../images/protrac2.png"
import img3 from "../../../../images/protrac3.png"



const Protrac = () => {
    return (
    <>
    <div className="title-btn">
    <a href="http://protrac08.herokuapp.com" rel="noreferrer" target="_blank"><button>view</button></a>
    <h1>ProTrac</h1>
    <a href="https://github.com/daniel-deru/protrac" rel="noreferrer" target="_blank"><button>code</button></a>
    </div>
  
 
    <div className="project-wrapper">
        <div className="preview">
            <div className="img-container">
                <img src={img1} className="project-img" alt=""/>
                <img src={img2}  className="project-img" alt=""/>
                <img src={img3}  className="project-img" alt=""/>
            </div>
        </div>
        <aside className="project-content">
            <p>This is a fullstack web app built for monitoring your productivity. I realy enjoyed working on this since it uses a database and proper server side logic. I enjoy putting things together and to make them work seamlessly. The goal was to make a simple UI that is easy to use but still performs all the functions for the app to work. this project was approached with the idea of future expandibility for when I want to add features easily without changing the base design. Tech Stack: React, Node, MySQL</p>
        </aside>
    </div>
    </>
    )
}

export default Protrac
