import React from 'react'
import {BrowserRouter, NavLink } from "react-router-dom"
import "./ProjectsController.css"
import { FaCircle } from "react-icons/fa"

const ProjectsController = () => {
    return (
        // <BrowserRouter>
        <div className="controller" >
            
            <ul>
               <NavLink activeClassName="project-active" className="project-nav"  to="/work/protrac"><li><FaCircle/></li></NavLink>
               <NavLink activeClassName="project-active" className="project-nav" to="/work/cryptograph"><li><FaCircle/></li></NavLink>
               <NavLink activeClassName="project-active" className="project-nav" to="/work/paintmagic"><li><FaCircle/></li></NavLink>
               <NavLink activeClassName="project-active" className="project-nav" to="/work/pokedex"><li><FaCircle/></li></NavLink>
            </ul>
        </div>
        // </BrowserRouter>
    )

  
}

export default ProjectsController
